import React, { Component } from 'react';
import Pokecard from './Pokecard'
import './Pokedex.css';

class Pokedex extends Component {
    // static defaultProps = {
    //     pokemons: [
    //         { id: 4, name: 'Charmander', type: 'fire', base_experience: 62 },
    //         { id: 7, name: 'Squirtle', type: 'water', base_experience: 63 },
    //         { id: 11, name: 'Metapod', type: 'bug', base_experience: 72 },
    //         { id: 12, name: 'Butterfree', type: 'flying', base_experience: 178 },
    //         { id: 25, name: 'Pikachu', type: 'electric', base_experience: 112 },
    //         { id: 39, name: 'Jigglypuff', type: 'normal', base_experience: 95 },
    //         { id: 94, name: 'Gengar', type: 'poison', base_experience: 225 },
    //         { id: 133, name: 'Eevee', type: 'normal', base_experience: 65 }
    //     ]
    // }
    render() {
        // console.dir(this.props);
        const { pokemons, isWinner, exp } = this.props;
        let gameResult;
        if (isWinner) {
            gameResult = <span className="Pokedex-winner">Winning hand</span>
        } else {
            gameResult = <span className="Pokedex-loser">Losing hand</span>
        }
        return (
            <div>
                <h2 className="Pokedex-gameResult">{gameResult} - total experience: {exp}</h2>
                <div className="Pokedex-container">
                    {pokemons.map((p, i) => (
                        <Pokecard key={p.id} id={p.id} name={p.name} type={p.type} exp={p.base_experience} delay={i * 300} />
                    ))}
                </div>
            </div>
        )
    }
}

export default Pokedex;

